.program-tabs__header-list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;

  list-style: none;
}

.program-tabs__header-item {
  position: relative;
  width: 228px;
  margin: 0;
  padding-top: 25px;
  padding-bottom: 24px;
  padding-left: 72px;

  cursor: pointer;
}

.program-tabs__title {
  margin: 0;

  @include tab-title;
}

.program-tabs__header-item--culture::before {
  @include program-tab-icon(url("../img/icons/culture-b.png"), 28px 33px);
}

.program-tabs__header-item--study::before {
  @include program-tab-icon(url("../img/icons/study-b.png"), 36px 35px);
}

.program-tabs__header-item--business::before {
  @include program-tab-icon(url("../img/icons/business-b.png"), 32px 25px);
}


.program-tabs__header-item--volonteur::before {
  @include program-tab-icon(url("../img/icons/volonteur-b.png"), 32px 29px);
}

.program-tabs__header-item--religion::before {
  @include program-tab-icon(url("../img/icons/religion-b.png"), 23px 25px);
}


.program-types__content-list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.program-tabs__content-header {
  @include modal-header;
}

.program-tabs__content-description {
  @include text-regular;

  opacity: 0.87;
}
